<template>
	<scroll-view scroll-y="true" class="container" :show-scrollbar="false">
		<view class="msg">
			请完成以下信息
		</view>
		<view class="info">
			<u--form labelPosition="left" :model="model" :rules="rules" ref="uForm" labelWidth="90">
				<u-form-item label="手机号码" prop="userInfo.mobile" borderBottom ref="item1">
					<u--input v-model="model.userInfo.mobile" border="none" placeholder="请填写手机号"></u--input>
				</u-form-item>
				<u-form-item label="开户银行" prop="userInfo.bank" borderBottom ref="item1">
					<u--input 
						v-model="model.userInfo.bank" 
						border="none" 
						placeholder="请选择开户银行"
						disabled
						disabledColor="#fff"
					></u--input>
					<u-icon
						slot="right"
						name="arrow-right"
					></u-icon>
				</u-form-item>
				<u-form-item label="银行卡号" prop="userInfo.bankCard" borderBottom ref="item1">
					<u--input v-model="model.userInfo.bankCard" border="none" placeholder="请填写银行卡号"></u--input>
				</u-form-item>
			</u--form>
		</view>
		<view class="remark">
			*手机号码必须是银行预留手机号
		</view>
		<view class="footer">
			<view class="">
				<u-button text="保 存" color="#FE542C" @click="handlerOk"></u-button>
			</view>
		</view>
	</scroll-view>
</template>

<script>
	export default {
		data() {
			return {
				model: {
					userInfo: {
						mobile: '',
						bank: '',
						bankCard: ''
					}
				},
				rules: {
					'userInfo.mobile': [
							{
								required: true, 
								message: '请填写手机号',
								trigger: ['change','blur'],
							},
							{
								validator: (rule, value, callback) => {
									return uni.$u.test.mobile(value);
								},
								message: '手机号码不正确',
								trigger: ['change','blur'],
							}
						],
						
						'userInfo.bank': {
							required: true,
							message: '请选择开户银行',
							trigger: ['blur', 'change']
						},
						'userInfo.bankCard': {
							required: true,
							message: '请填写银行卡号',
							trigger: ['blur', 'change']
						}
				}
			}
		},
		
		onReady() {
				//onReady 为uni-app支持的生命周期之一
		    	this.$refs.uForm.setRules(this.rules)
		},
		methods: {
			handlerOk() {
				// this.$refs.uForm.validate().then(res => {
				// 	uni.$u.toast('校验通过')
				// }).catch(errors => {
				// 	uni.$u.toast('校验失败')
				// })
				uni.reLaunch({
					url: '/pages/whiteBar/whiteBar'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.container {
		font-size: 14px;
		color: #333;
		height: 100vh;

		.msg {
			height: 36rpx;
			font-size: 25rpx;
			font-family: PingFangSC, PingFang SC;
			font-weight: 400;
			color: #999999;
			line-height: 36rpx;
			letter-spacing: 1px;
			padding: 24rpx 0 12rpx 24rpx;
		}
		
		.info {
			background-color: #fff;
			padding: 0 28rpx 0 24rpx;
		}
	}
	
	.remark {
		height: 36rpx;
		font-size: 25rpx;
		font-family: PingFangSC, PingFang SC;
		font-weight: 400;
		color: #999;
		line-height: 36rpx;
		padding-left: 24rpx;
		padding-top: 20rpx;
	}
	.footer {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		z-index: 1;
		view {
			padding: 36rpx;
		}
	}
</style>
